<template>
  <div style="font-size: 16px">
    <el-row style="margin-bottom: 20px;margin-top: 5px">
      <el-col :span="2" style="text-align: right;margin-top: 8px">
        &nbsp;&nbsp;
      </el-col>
      <el-col :span="18">
        <el-input v-model="requestData.title" placeholder=""></el-input>
      </el-col>
    </el-row>
    <el-row style="margin-bottom: 20px">
      <el-col :span="2" style="text-align: right;margin-top: 8px">
        &nbsp;&nbsp;
      </el-col>
      <el-col :span="20">
        <el-col :span="12">
          <el-input v-model="requestData.label" placeholder=""></el-input>
        </el-col>
        <el-col :span="8" style="font-size: 12px;margin-top: 20px;color: #5e6d82">
          ('/')
        </el-col>
      </el-col>
    </el-row>
    <el-row style="margin-bottom: 20px">
      <el-col :span="2" style="text-align: right;margin-top: 8px">
        &nbsp;&nbsp;
      </el-col>
      <el-col :span="20">
        <el-input
          type="textarea"
          :rows="3"
          placeholder=""
          v-model="requestData.articleAbstract">
        </el-input>
      </el-col>
    </el-row>
    <el-row style="margin-bottom: 20px">
      <el-col :span="2" style="text-align: right;margin-top: 8px">
        &nbsp;&nbsp;
      </el-col>
      <el-col :span="20">
        <el-date-picker
          v-model="requestData.pushTime"
          type="datetime"
          placeholder=""
          align="right"
          :picker-options="pickerOptions">
        </el-date-picker>
      </el-col>
    </el-row>
    <el-row style="margin-bottom: 20px">
      <el-col :span="2" style="text-align: right;margin-top: 8px">
        &nbsp;&nbsp;
      </el-col>
      <el-col :span="20">
        <el-upload
          ref="icon"
          action="#"
          accept="image/png,image/gif,image/jpg,image/jpeg"
          list-type="picture-card"
          :limit=1
          :auto-upload="false"
          :file-list="fileList"
          :on-exceed="exceedFile"
          :on-success="handleSuccess01"
          :on-preview="handlePreview01"
          :on-remove="handleRemove"
          :before-upload="beforeUpload">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-col>
    </el-row>
    <el-row style="margin-bottom: 40px">
      <el-col :span="2" style="text-align: right;margin-top: 8px">
        &nbsp;&nbsp;
      </el-col>
      <el-col :span="20">
        <vue-ueditor-wrap
          @ready="editorReady"
          v-model="requestData.msgTXT"
          :config="myConfigTXT"></vue-ueditor-wrap>
      </el-col>
    </el-row>
    <el-row style="margin-bottom: 0px">
      <el-col :span="21" style="text-align: right">
        <el-button type="primary" @click="clickSubmitBtn()"></el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'addInternationalNews',
    data () {
      return {
        isAdd: 1,
        isUpdate: 1,
        fileList: [],
        dialogVisible: false,
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        requestData: {
          managerID: '',
          title: '',
          label: '',
          type: '',
          articleAbstract: '',
          pushTime: '',
          imgBase: '',
          msgTXT: '<h2><img src="http://img.baidu.com/hi/jx2/j_0003.gif"/>Vue + UEditor + v-model</h2>',
        },
        //
        myConfigTXT: {
          // 
          autoHeightEnabled: false,
          // 
          initialFrameHeight: 900,
          // 
          initialFrameWidth: 900,
          // 
          serverUrl: 'http://localhost:8080/UEditor-jsp/jsp/controller.jsp',

          /*UEDITOR_HOME_URL: '/UEditor/'*/
        }
      }
    },
    components: {},

    methods: {
      // 
      exceedFile(files, fileList) {
        this.$notify.warning({
          title: '',
          message: `1 ${files.length + fileList.length} `
        });
      },
      editorReady(editorInstance) {
        console.log(`${editorInstance.key}: `, editorInstance)
      },
      // 
      handleSuccess01(res, file, fileList) {
        this.$notify.success({
          title: '',
          message: ``
        });
      },
      handleRemove(){
        this.isUpdate = 2;
      },
      handlePreview01(file){
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      beforeUpload(file) {
        var _this = this;
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function(e) {
          // base64
          var newUrl = this.result;    //
          _this.requestData.imgBase = this.result;
          _this.requestData.type = 2;
          if (_this.isAdd == 1){
            _this.addNews();
          } else {
            _this.updateNews();
          }
        };


      },
      updateBase(file){
        var _this = this;
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function(e) {
          // base64
          var newUrl = this.result;    //
          alert(newUrl)
          _this.imgUrl = newUrl;
        };
      },
      clickSubmitBtn(){
        this.$refs.icon.submit();
        if (this.isUpdate==1&&this.isAdd==2) {
          this.updateNews();
        }
      },
      addNews(){
        this.$post(this.$api.module.operationNews,this.requestData).then(res => {
          if (res.status == 200){
            this.$message({
              message: '',
              type: 'success'
            });
            this.refreshData();
            this.$router.push({name: 'nationalNewsDraftBox'});
          } else {
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      },
      updateNews(){
        this.$put(this.$api.module.operationNews + '/' + this.requestData.newsID,this.requestData).then(res => {
          if (res.status == 200){
            this.$message({
              message: '',
              type: 'success'
            });
            this.refreshData();
            if(this.requestData.status == 1){
              this.$router.push({name: 'upadteNationalNews'});
            }else {
              this.$router.push({name: 'nationalNewsDraftBox'});
            }
          } else {
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      },
      refreshData(){
        this.requestData = {
          managerID: '',
          title: '',
          label: '',
          type: '',
          articleAbstract: '',
          pushTime: '',
          imgBase: '',
          msgTXT: '<h2><img src="http://img.baidu.com/hi/jx2/j_0003.gif"/>Vue + UEditor + v-model</h2>',
        };
      }
    },
    mounted () {
      this.requestData.managerID = this.getCookie('manageID');
      let updateData = this.$route.params.updateData;
      if (updateData){
        this.isAdd = 2;
        let newsID = updateData.newsID
        this.$get(this.$api.module.operationNews + '/' + newsID).then(res => {
          if (res.status == 200){
            this.requestData = res.data;
            this.fileList.push(
              {
                name: this.requestData.photoUrl,
                url: require('D:/IDEA//img/' + this.requestData.photoUrl)
              }
            )
            this.$message({
              message: '',
              type: 'success'
            });
          } else {
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }
      document.cookie="tyle="+"1";
    }
  }
</script>

<style scoped>

</style>




